import { INTERACTION_CLASS_NAME } from "@rp/react/web/chat"

export const scrollToInteraction = (el: HTMLDivElement | null, interactionTimestamp?: string) => {
  const firstChild = el?.children[0] as HTMLDivElement | null
  if (!firstChild || !el) return
  const parentRect = firstChild.getBoundingClientRect()
  const all = el.querySelectorAll(`.${INTERACTION_CLASS_NAME}`)
  const last = el.querySelector(`.interaction-${interactionTimestamp}`) ?? all[all.length - 1]
  const interactionContainer = last?.getBoundingClientRect()

  setTimeout(() => {
    interactionContainer &&
      el.scroll({
        top: interactionContainer.top - parentRect.top - 16,
        behavior: "smooth",
      })
  }, 100)
}
